  }
}

.main-slider {
  .swiper-button {
    width: 13%;
    height: 35%;
    background-size: 0 0 !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    transition: background-size 2s ease-in;

    &:hover {
      background-size: cover !important;
    }

    svg {
      fill: $white;
      width: 50px;
      position: relative;
      top: 100px;
      transition: .5s linear;
    }
  }

  .swiper-button-next {
    bottom: -50px;
    position: absolute;
    left: 13%;
    top: auto;

    svg {
      transform: rotate(180deg);
    }
  }

  .swiper-button-prev {
    bottom: -50px;
    position: absolute;
    left: 0;
    top: auto;
  }

  .progressbar-slide-index {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-weight: 600;
    color: #093868;
    bottom: 40px;
    right: 500px;
    position: absolute;
  }

  .progressbar-slide-total {
    font-size: 14px;
    font-weight: 600;
    color: #093868;
    bottom: 40px;
    right: 274px;
    position: absolute;
  }
}

body .swiper-button-prev:after {
  content: '';
}

body .swiper-button-next:after {
  content: '';
}

.main-slider .swiper-button.swiper-button-disabled {
  display: none;
}



.main-slider {
  .slider-counter {
    font-weight: 700;
    display: flex;
    align-items: flex-start;
    position: absolute;
    left: 50%;

    .current-slide {
      font-size: 40px;
      color: $red;
      line-height: 0.8;
    }

    .total-slides {
      font-size: 14px;
      color: $white;
      position: relative;
      letter-spacing: 2.8px;

      &::before {
        position: absolute;
        content: '';
