.blog-item {
	margin-bottom: 30px;
  transition: $trans;
  position: relative;

  &_media {
		line-height: 0;
		&::after {
			transition: 0.2s ease;
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(9,56,104,0.7) 100%);
		}

    img {
      max-width: 100%;
    }
  }

  &_content {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 40px;
  }

  &:hover {
		box-shadow: 0px 20px 30px 0 rgba(9, 41, 75, 0.35);
		.blog-item_media {

			&::after {
				background: linear-gradient(180deg, rgba(9,56,104,0.9) 0%, rgba(9,56,104,0.9) 100%);
			}
		}
    .blog-item-main-content {
      bottom: 0;
    }

    .blog-item_descr {
      opacity: 1;
      bottom: 0px;
    }

    .read_more {
      opacity: 1;
      bottom: 0px;

    }
  }
}

.blog-item_content {
  max-width: 450px;
  color: $white;

  date {
    position: relative;
    bottom: 0;

    display: inline-block;
    text-transform: uppercase;
