			padding-bottom: 10px;
			padding-top: 6px;
      bottom: 0;
      font-family: Quicksand;
      font-size: 18px;
      font-weight: bold;
			line-height: 1.43;
			&.title_large {
				font-size: 28px;
			}
		}

